.container {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  text-align: center;
  height: 100%;
}

.content {
  margin: 0;
  width: 100%;
  position: relative;
  min-height: 100vh;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 80px 40px;
  height: 100%;
}

.BgImage {
  background-image: url("../../assets/images/login-background.jpg");
  height: 100%;
  background-position: top;
  background-size: cover;
  background-repeat: no-repeat;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: -1;
}
.CTA {
  margin-bottom: 2vw;
  max-width: 600px !important;
  flex-wrap: wrap;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0;
  text-align: center;
  margin-right: auto;
  margin-left: auto;
  transition-timing-function: ease-out;
  transition: opacity 02s;
  width: 100%;
}

.buttonsignup {
  max-width: 600px;
  font-weight: bold;
  color: white;
  background-color: rgb(26, 79, 226);
  border: 1px solid #ffffff00;
  border-radius: 10px;
  width: 100%;
  letter-spacing: 1.5px;
  font-size: 18px;
  padding: 16.5px 0;
}
.buttonsignup:hover {
  background-color: rgb(19, 62, 182);
}

.description1 {
  max-width: 600px;
  color: rgba(255, 255, 255, 0.808);
  font-size: 13px;
  margin: 0 0 24px;
  line-height: 1.5;
  letter-spacing: 1.5px;
  font-weight: bold;
}

.CTALogoOne {
  margin-bottom: 12px;
  max-width: 600px;
  min-height: 1px;
  display: block;
  width: 100%;
}
.CTALogoTwo {
  margin-bottom: 12px;
  max-width: 600px;
  min-height: 1px;
  display: block;
  width: 100%;
}

.nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 50px;
  background-color: rgb(37, 37, 37);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 36px;
}

.logo {
  padding: 0;
  width: 100px;
  margin-top: 4px;
  max-height: 50px;
  font-size: 0;
}

.navmenu {
  align-items: center;
  display: flex;
  flex-flow: row nowrap;
  height: 100%;
  margin: 0;
  padding: 0;
  position: relative;
  margin-right: auto;
  margin-left: 20px;
}

@media (max-width: 768px) {
  .navmenu {
    display: none;
  }
}

.home {
  display: flex;
  padding: 0 12px;
  align-items: center;
  color: rgb(249, 249, 249);
  font-size: 15px;
  letter-spacing: 1px;
  line-height: 1.08;
  white-space: nowrap;
  position: relative;
}
